.navbar{
    background-color: var(--navbar-dark);
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 2rem 1rem 2rem;
    box-sizing: border-box;
    z-index: 999;
}

.logo{
    color: white;
    text-decoration: none;
    font-size: 1.3em;
    padding-left: 2rem;
}

.navbar-categories-router{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    column-gap: 2rem;
}

.navbar-list-router{
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
}

.nav-link{
    padding-right: 2rem;
    text-decoration: none;
    color: var(--navbar-link-grey);
    font-size: 1rem;
}

.nav-link:hover{
    color: white;
}

.sticky {
    position: fixed;
    width: 100%;
}

@media (max-width: 1200px){
    .logo{
        font-size: 1.1rem;
    }
    .nav-link{
        font-size: 0.9rem;
    }
}

@media (max-width: 700px){
    .logo{
        font-size: 1rem;
        padding-left: 1rem;
    }
    .nav-link{
        display: none;
    }
}